body {
  margin: 0;
}

nav {
  height: 40px;
  background-color: $color1;
}

ul {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  height: 100%;
  width: 80%;
  margin: 0 auto;
}

li {
  list-style: none;
}
li:not(:last-child) {
  margin-right: 25px;
}

li > a {
  color: #fff;
  text-decoration: none;
  font-family: $font1;
}

header {
  position: fixed;
  width: 100%;
  top: 0;
}

main {
  width: 80%;
  margin: 0 auto;
}

h1 {
  font-size: 40px;
  text-align: center;
  padding: 40px 0;
  color: $color1;
}

h2 {
  font-size: 25px;
  padding: 40px 0;
  color: $color2;
}

#topWindow {
  width: 30px;
  height: 30px;
  position: fixed;
  right: 35px;
  bottom: 35px;
  display: none;
  font-size: 40px;
}
.fa-arrow-circle-up {
  color: $color3;
}

.content_select {
  width: calc(80%+10px);
  margin: 100px auto 0;
  @include flex(column, center, center);
  select {
    width: 200px;
    font-family: $font1;
  }
}
article {
  @extend %center_section;
  @extend .fa-arrow-circle-up;
}
